		.js .inputfile {
		    width: 0.1px;
		    height: 0.1px;
		    opacity: 0;
		    overflow: hidden;
		    position: absolute;
		    z-index: -1;
		}

		.inputfile + label {
		    font-size: 1.25rem;
		    /* 20px */
		    font-weight: 700;
		    text-overflow: ellipsis;
		    white-space: nowrap;
		    cursor: pointer;
		    display: inline-block;
		    overflow: hidden;
		    padding: 0.625rem 1.25rem;
		    /* 10px 20px */
		}
		
		.magic-container
			width 100%
			margin-top -30px

		.no-js .inputfile + label {
		    display: none;
		}
		
		.form-control.inputfile.inputfile-6
			opacity 0

		.inputfile:focus + label,
		.inputfile.has-focus + label {
		    outline: 1px dotted #000;
		    outline: -webkit-focus-ring-color auto 5px;
		}

		.inputfile + label * {
		    /* pointer-events: none; */
		    /* in case of FastClick lib use */
		}

		.inputfile + label svg {
		    width: 1em;
		    height: 1em;
		    vertical-align: middle;
		    fill: currentColor;
		    margin-top: -0.25em;
		    /* 4px */
		    margin-right: 0.25em;
		    /* 4px */
		}

		/* style 6 */
		.inputfile-6 + label {
			color: #918F90;
		}

		.inputfile-6 + label {
			border: 1px solid #918F90;
			background-color: #fff;
			padding: 0;
		}
		
		.inputfile-6:focus + label,
		.inputfile-6.has-focus + label,
		.inputfile-6 + label:hover {
			border-color: #918F90;
		}

		.inputfile-6 + label span,
		.inputfile-6 + label strong {
			padding: 0.625rem 1.25rem;
			/* 10px 20px */
		}

		.inputfile-6 + label span {
			width: 200px;
			min-height: 2em;
			display: inline-block;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
			vertical-align: top;
		}

		label span {
			width: 100%;
			min-height: 2em;
			display: inline-block;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
			vertical-align: top;
		}

		.inputfile-6 + label strong {
			height: 100%;
			color: #fff;
			float: right;
			background-color: #918F90;
			display: inline-block;
		}

		.inputfile-6:focus + label strong,
		.inputfile-6.has-focus + label strong,
		.inputfile-6 + label:hover strong {
			background-color: #ccc;
		}

		@media screen and (max-width: 50em) {
			.inputfile-6 + label strong {
				display: block;
			}
		}
